import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Priority} from '../../model/Priority';
import {MatDialog} from '@angular/material';
import {EditPriorityDialogComponent} from '../../dialog/edit-priority-dialog/edit-priority-dialog.component';
import {DataHandlerService} from '../../service/data-handler.service';
import {ConfirmDialogComponent} from '../../dialog/confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-priorities',
  templateUrl: './priorities.component.html',
  styleUrls: ['./priorities.component.css']
})
export class PrioritiesComponent implements OnInit {
  static defaultColor = '#fff';

  @Input()
  private priorities: [Priority];

  @Output()
  addPriority = new EventEmitter<Priority>();
  @Output()
  updatePriority = new EventEmitter<Priority>();
  @Output()
  deletePriority = new EventEmitter<Priority>();

  constructor(
    private dialog: MatDialog
  ) { }

  ngOnInit() {

  }

  private onAddPriority() {
    const dialogRef = this.dialog.open(EditPriorityDialogComponent, {data: ['', 'Добавление приоритета', true], width: '400px'});
    dialogRef.afterClosed().subscribe(res => {
      if (res) {
        const newPriority = new Priority(null, res as string, PrioritiesComponent.defaultColor);
        this.addPriority.emit(newPriority);
      }
    });

  }

  private onDeletePriority(priority: Priority) {
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      maxWidth: '500px',
      data: {
        dialogTitle: 'Подтвердите действие',
        message: 'Вы действительно хотите удалить категорию?'
      },
      autoFocus: false
    });
    dialogRef.afterClosed().subscribe(res => {
      if (res) {
        this.deletePriority.emit(priority);
      }
    });

  }

  private onEditPriority(priority: Priority) {
    const dialogRef = this.dialog.open(EditPriorityDialogComponent, {data: [priority.title, 'Изменить приоритет', true], width: '400px'});
    dialogRef.afterClosed().subscribe( res => {
      if (res === 'delete') {
        this.deletePriority.emit(priority);
        return;
      }
      if (res) {
        priority.title = res as string;
        this.updatePriority.emit(priority);
        return;
      }
    });
  }
}
