import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {DataHandlerService} from '../../service/data-handler.service';
import {Category} from '../../model/Category';
import {MatDialog} from '@angular/material';
import {EditCategoryDialogComponent} from '../../dialog/edit-category-dialog/edit-category-dialog.component';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent implements OnInit {
  @Input()
  categories: Category[];
  @Output()
  selectCategory = new EventEmitter<Category>();
  @Input()
  selectedCategory: Category;

  private indexMouseMove: number;
  @Output()
  private deleteCategory = new EventEmitter<Category>();
  @Output()
  private updateCategory = new EventEmitter<Category>();
  @Output()
  private addCategory = new EventEmitter<string>();
  private searchCategoryText: string;
  @Output()
  private searchCategory = new EventEmitter<string>();
  @Input()
  uncompletedTotal: number;


  private selectedCategoryMap: Map<Category, number>;

  @Input('categoryMap')
  set setCategoryMap(categoryMap: Map<Category, number>) {
    this.selectedCategoryMap = categoryMap;
  }


  constructor(private dataHandler: DataHandlerService,
              private dialog: MatDialog
              ) {
  }

  ngOnInit() {
    // this.dataHandler.getAllCategories().subscribe(c => this.categories = c);
  }

  showByCategory(category: Category) {
    if (this.selectedCategory === category) {
      return;
    }
    this.selectedCategory = category;
    this.selectCategory.emit(this.selectedCategory);
  }

  showEditIcon(index: number) {
    this.indexMouseMove = index;
  }

  openEditCategoryDialog(category: Category) {
    const dialogRef = this.dialog.open(EditCategoryDialogComponent,{data: [category.title, 'Редактирование категории'], width: '400px'});
    dialogRef.afterClosed().subscribe(res => {
      if (res === 'delete') {
        this.deleteCategory.emit(category);
        return;
      }
      if (res as string) {
        category.title = res as string;
        this.updateCategory.emit(category);
        return;
      }
    });
  }

  openAddDialog() {
    const dialogRef = this.dialog.open(EditCategoryDialogComponent, {data: ['', 'Добавление категории'], width: '400px'});
    dialogRef.afterClosed().subscribe( res => {
      if (res) {
        this.addCategory.emit(res as string);
      }
    });
  }

  onFilterByCategory() {
    if (this.searchCategoryText == null) {
      return;
    }
    this.searchCategory.emit(this.searchCategoryText);
  }
}
