import {Component, Inject, OnInit} from '@angular/core';
import {DateAdapter, MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {Task} from '../../model/Task';
import {DataHandlerService} from '../../service/data-handler.service';
import {Category} from '../../model/Category';
import {Priority} from '../../model/Priority';
import {ConfirmDialogComponent} from '../confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-edit-task-dialog',
  templateUrl: './edit-task-dialog.component.html',
  styleUrls: ['./edit-task-dialog.component.css']
})
export class EditTaskDialogComponent implements OnInit {
  private dialogTitle: string;
  private task: Task;
  private tmpTitle: string;
  private categories: Category[];
  private tmpCategory: Category;
  private priorities: Priority[];
  private tmpPriority: Priority;
  private tmpDate: Date;
  private taskStatus = true;


  constructor(
    private dialogRef: MatDialogRef<EditTaskDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: [Task, string],
    private dataHandler: DataHandlerService,
    private dialog: MatDialog,
    private adapter: DateAdapter<any>
  ) { }

  ngOnInit() {
    this.task = this.data[0];
    this.dialogTitle = this.data[1];
    this.tmpTitle = this.task.title;
    this.tmpCategory = this.task.category;
    this.tmpPriority = this.task.priority;
    this.adapter.setLocale('ru-RU');
    // @ts-ignore
    this.tmpDate = this.task.date;

    this.dataHandler.getAllPriorities().subscribe(p => this.priorities = p);
    this.dataHandler.getAllCategories().subscribe( c => this.categories = c);
    this.checkTaskStatus();
  }

  onConfirm() {
    this.task.title = this.tmpTitle;
    this.task.category = this.tmpCategory;
    this.task.priority = this.tmpPriority;
    this.task.date = this.tmpDate;
    this.dialogRef.close(this.task);
  }

  onCancel() {
    this.dialogRef.close(null);
  }

  onDelete() {
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      maxWidth: '500px',
      data: {
        dialogTitle: 'Подтвердите действие',
        message: `Вы действительно хотите удалить задачу: "${this.task.title}" ?`
      },
      autoFocus: false
    });
    dialogRef.afterClosed().subscribe(res => {
      if (res) {
        this.dialogRef.close('delete');
      }
    });
  }

  onStatusTask(bool: boolean) {
    console.log(bool);
    let status: string;
    if (bool === true) {
      status = 'activate';
    } else {
      status = 'complete';
    }
    this.dialogRef.close(status);
  }

  private checkTaskStatus() {
    if (this.task.title === null || this.task.title === '') {
      this.taskStatus = false;
    }
  }
}
