import {Injectable, NgZone} from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { Observable } from 'rxjs';
import { auth } from 'firebase/app';
import {Router} from '@angular/router';
import * as firebase from 'firebase';

@Injectable({
  providedIn: 'root'
})

export class AuthenticationService {
  userData: Observable<firebase.User>;
  user$: Observable<any>;
  errorMessage: string;

  constructor(
    private angularFireAuth: AngularFireAuth,
    public ngZone: NgZone,
    public router: Router
              ) {
    this.userData = angularFireAuth.authState;
  }

  /* Sign up */
  async SignUp(email: string, password: string) {
    this.angularFireAuth
      .auth
      .createUserWithEmailAndPassword(email, password)
      .then(res => {
        console.log('You are Successfully signed up!', res);
      })
      .catch(error => {
        if (error.code === 'auth/email-already-in-use') {
          this.errorMessage = 'Пользователь уже существует';
        } else if (error.code === 'auth/invalid-email') {
          this.errorMessage = 'Неправильный email';
        } else if (error.code === 'auth/weak-password') {
          this.errorMessage = 'Пароль должен содержать не менее 6 символов';
        }
        console.log('Something is wrong:', error.message);
        console.log(error.code);
      });
  }

  /* Sign in */
  async SignIn(email: string, password: string) {
    this.angularFireAuth
      .auth
      .signInWithEmailAndPassword(email, password)
      .then(res => {
        console.log('You are Successfully logged in!');
      })
      .catch(err => {
        this.errorMessage = err.message;
        if (err.code === 'auth/invalid-email') {
          this.errorMessage = 'Неправильный формат почты';
        } else if (err.code === 'auth/wrong-password') {
          this.errorMessage = 'Неправильный пароль';
        } else if (err.code === 'auth/user-not-found') {
          this.errorMessage = 'Такой пользователь не найден';
        }
        console.log('Something is wrong:', err.message);
        console.log(err);
      });
  }

  /* Sign out */
  SignOut() {
    this.angularFireAuth
      .auth
      .signOut();
  }

  OAuthProvider(provider) {
    return this.angularFireAuth.auth.signInWithPopup(provider)
      .then((res) => {
        this.ngZone.run(() => {
          this.router.navigate(['']);
        });
      }).catch((error) => {
        window.alert(error);
      });
  }

// Firebase Google Sign-in
  SigninWithGoogle() {
    return this.OAuthProvider(new auth.GoogleAuthProvider())
      .then(res => {
        console.log('Successfully logged in!');
      }).catch(error => {
        console.log(error);
      });
  }
}
